import React from 'react';
import { Col, Form, InputGroup } from 'react-bootstrap';

interface Props {
  disabled?: boolean;
  defaultValue?: any;
  mdCol?: string;
  label?: string;
  name?: string;
  values?: any;
  onChange?: (e: any) => void;
  isValid?: boolean;
  feedback?: any;
  placeholder?: string;
  icon: any;
}

const TextIconInput = (props: Props) => {
  return (
    <Form.Group
      name={props.name}
      className="mb-3"
      as={Col}
      md={props.mdCol || '6'}
      controlId="validationFormik01"
    >
      <Form.Label>{props.label}</Form.Label>
      <InputGroup className="input-with-prefix">
        <div className="text-muted icon-prefix">{props.icon}</div>
        <Form.Control
          disabled={props.disabled}
          style={{ paddingLeft: `36px` }}
          type="text"
          defaultValue={props.defaultValue}
          name={props.name}
          value={props.values}
          onChange={props.onChange}
          isValid={props.isValid}
        />
      </InputGroup>
      {props.feedback}
      <style scoped>{`
      .input-with-prefix {
        position: relative;
      }
      .icon-prefix {
        position: absolute;
        z-index: 1200;
        top: 6px;
        left: 8px

      }
      `}</style>
    </Form.Group>
  );
};

export default TextIconInput;
