import React from 'react';
import NotificationsOutlinedIcon from '@mui/icons-material/NotificationsOutlined';
import { Image, Button, Dropdown, ButtonGroup } from 'react-bootstrap';
import { routers } from 'router/router';
import { useHistory } from 'react-router';
import Cookies from 'js-cookie';
import { useAppDispatch, useAppSelector } from 'app/hooks';
import { authenticateSettings } from 'components/helper/appConfig';
import PowerSettingsNewIcon from '@mui/icons-material/PowerSettingsNew';
import SettingsIcon from '@mui/icons-material/Settings';
import { setMyAuthorizationEmail } from 'app/authorization/authorizationSlice';
interface Props {
  title: string;
}
const Header = (props: Props) => {
  const dispatch = useAppDispatch();
  let history = useHistory();

  const handleLogout = () => {
    Cookies.remove(authenticateSettings.tokenName);
    history.push(routers.login.path);
  };

  const displayEmail = useAppSelector((store) => store.authorization.myAuthorizationEmail);
  React.useEffect(() => {
    if (!displayEmail) {
      dispatch(setMyAuthorizationEmail(localStorage.getItem('currentUserEmail')));
    }
  }, [displayEmail, dispatch]);
  return (
    <div className="w-100 d-flex justify-content-between align-items-center border-bottom">
      <div className="w-50">
        <img src={'/assets/images/Logo.svg'} alt="sb-logo" className="border-end mx-2 px-2" />
        <span className="text-muted h4" style={{ fontWeight: 600 }}>
          {props.title}
        </span>
      </div>
      <div className="w-50 d-flex justify-content-end align-items-center">
        <span className="mx-3">
          <Button variant="outline-dart" className="border rounded" size="sm">
            <NotificationsOutlinedIcon />
          </Button>
        </span>
        <span>
          <Image src="https://picsum.photos/200" roundedCircle width="36px" />
        </span>
        <span>
          <Dropdown as={ButtonGroup} size="sm">
            <Dropdown.Toggle variant="white" id="dropdown-split-basic">
              <span>
                {displayEmail}
                <div className="text-muted">10th Grader</div>
              </span>
            </Dropdown.Toggle>

            <Dropdown.Menu>
              {/* <Dropdown.Item onClick={() => history.push(routers.register.path)}>
                To register page
              </Dropdown.Item>
              <Dropdown.Item onClick={() => history.push(routers.login.path)}>
                To login page
              </Dropdown.Item> */}
              <Dropdown.Item onClick={() => {}}>
                <SettingsIcon fontSize="small" className="mx-1" />
                Setting
              </Dropdown.Item>
              <Dropdown.Item onClick={() => handleLogout()}>
                <PowerSettingsNewIcon fontSize="small" className="mx-1" />
                Logout
              </Dropdown.Item>
            </Dropdown.Menu>
          </Dropdown>
        </span>
      </div>
      <style scoped>{`
      .dropdown-toggle::after {
        display: none !important;
      }
      `}</style>
    </div>
  );
};

export default Header;
