import React from 'react';
import { Col, Form } from 'react-bootstrap';

interface Props {
  mdCol?: string;
  label?: string;
  name: string;
  values: any;
  onChange: (e: any) => void;
  isValid?: boolean;
  feedback?: any;
  placeholder?: string;
  disabled?: boolean;
}

const TextInput = (props: Props) => {
  return (
    <Form.Group
      name={props.name}
      className="mb-3"
      as={Col}
      md={props.mdCol || '6'}
      controlId="validationFormik01"
    >
      <Form.Label>{props.label}</Form.Label>
      <Form.Control
        disabled={props.disabled}
        placeholder={props.placeholder}
        type="text"
        name={props.name}
        value={props.values}
        onChange={props.onChange}
        isValid={props.isValid}
      />
      {props.feedback}
    </Form.Group>
  );
};

export default TextInput;
