import React from 'react';
import { Form } from 'react-bootstrap';

interface Props {
  isActive: boolean;
  name: string;
  label: string;
  value: any;
}

const RadioInputRounded = (props: Props) => {
  return (
    <div
      className={
        props.isActive
          ? 'border rounded py-2 px-2 d-inline-block radio-checked-style pe-3'
          : 'border rounded py-2 px-2 d-inline-block pe-3'
      }
    >
      <Form.Check
        type="radio"
        defaultChecked={props.isActive}
        label={props.label}
        name={props.name}
        value={props.value}
        id={props.name + props.value}
      />
      <style scoped>{`
        .radio-checked-style {
          border-color: #5F30E2 !important;
          background-color: #F5F1FF !important
        }
        .form-check-input:checked {
          background-color: #5F30E2 !important;
          border-color: #5F30E2 !important;
        }
      `}</style>
    </div>
  );
};

export default RadioInputRounded;
