import React from 'react';
import { Col, Form, InputGroup } from 'react-bootstrap';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import VisibilityIcon from '@mui/icons-material/Visibility';
interface Props {
  mdCol: string;
  label: string;
  name: string;
  values: any;
  onChange: (e: any) => void;
  isValid?: boolean;
  feedback?: any;
}

const PasswordInput = (props: Props) => {
  const [view, setView] = React.useState(false);
  return (
    <Form.Group className="mb-3" as={Col} md={props.mdCol || '6'} controlId="validationFormik01">
      <Form.Label>{props.label}</Form.Label>
      <InputGroup className="input-with-prefix">
        <LockOutlinedIcon className="text-muted icon-prefix" />
        {view ? (
          <VisibilityOffIcon className="text-muted icon-sufix" onClick={() => setView(false)} />
        ) : (
          <VisibilityIcon className="text-muted icon-sufix" onClick={() => setView(true)} />
        )}

        <Form.Control
          style={{ paddingLeft: `36px` }}
          type={view ? 'text' : 'password'}
          name={props.name}
          value={props.values}
          onChange={props.onChange}
          isValid={props.isValid}
        />
      </InputGroup>
      {props.feedback}
      <style scoped>{`
      .input-with-prefix {
        position: relative;
      }
      .icon-prefix {
        position: absolute;
        z-index: 1200;
        top: 6px;
        left: 8px
      }
      .icon-sufix {
        position: absolute;
        z-index: 1200;
        top: 6px;
        right: 8px
      }
      `}</style>
    </Form.Group>
  );
};

export default PasswordInput;
