import React from 'react';
import { ProgressBar } from 'react-bootstrap';

interface Props {
  value: number;
}

const ProcessWithLable = (props: Props) => {
  return (
    <div className="d-flex flex-wrap align-items-center w-100">
      <span className="text-success sized70" style={{ minWidth: 35 }}>
        {props.value + ' %'}
      </span>
      <span className="text-center" style={{ width: 'calc(100% - 35px)' }}>
        <ProgressBar
          variant="success"
          className="w-100"
          now={props.value}
          style={{ height: '3px' }}
        />
      </span>
    </div>
  );
};

export default ProcessWithLable;
