import React from 'react';
import DashboardOutlinedIcon from '@mui/icons-material/DashboardOutlined';
import ContentPasteOutlinedIcon from '@mui/icons-material/ContentPasteOutlined';
import SmsOutlinedIcon from '@mui/icons-material/SmsOutlined';
import { Button, OverlayTrigger, Tooltip } from 'react-bootstrap';
import NestedList from './listMenu';
import { routers } from 'router/router';
import { useHistory } from 'react-router';
interface Props {
  hideList?: boolean;
}

const SideBar = (props: Props) => {
  const history = useHistory();
  return (
    <div className="w-100 h-100 d-flex">
      <div
        className="border-end d-flex flex-column justify-content-start align-items-center h-100"
        style={{ width: '48px' }}
      >
        <OverlayTrigger
          placement={'right'}
          overlay={
            <Tooltip id={`tooltip-dashboard`}>
              <strong>Dashboard</strong>
            </Tooltip>
          }
        >
          <Button variant="outline-light" onClick={() => history.push(routers.dashBoard.path)}>
            <DashboardOutlinedIcon className="my-3 text-muted" />
          </Button>
        </OverlayTrigger>
        <OverlayTrigger
          placement={'right'}
          overlay={
            <Tooltip id={`tooltip-application`}>
              <strong>Application</strong>
            </Tooltip>
          }
        >
          <Button
            variant="outline-light"
            onClick={() => history.push(routers.basicInformation.path)}
          >
            <ContentPasteOutlinedIcon className="my-3 text-muted" />
          </Button>
        </OverlayTrigger>
        <OverlayTrigger
          placement={'right'}
          overlay={
            <Tooltip id={`tooltip-settings`}>
              <strong>Settings</strong>
            </Tooltip>
          }
        >
          <Button variant="outline-light">
            <SmsOutlinedIcon className="my-3 text-muted" />
          </Button>
        </OverlayTrigger>
      </div>
      {!props.hideList && (
        <div className="w-100">
          <NestedList />
        </div>
      )}
    </div>
  );
};

export default SideBar;
