import http from "./http"

const authorizationServices = {
  async checkVerificationCode (body:any): Promise<any> {
    const res = http.post('api/students/check-verification-code', body)
    return res
    
  },
  async sendVerificationCode(body: any): Promise<any> {
    const res = http.post('api/students/send-verification-code', body)
    return res
  },
  
  async createStudent(body: any): Promise<any> {
    const res = http.post('api/students/create-student', body)
    return res
  },

  async login(body: any): Promise<any> {
    const res = http.post('api/auth/login', body)
    return res
  },

  async sendEmailForgotPassword(body: any): Promise<any> {
    const res = http.post('api/students/forgot-password', body)
    return res
  },

  async checkCodeForgotPassword(body: any): Promise<any> {
    const res = http.post('api/students/check-forgot-password-code', body)
    return res
  },

  async resetPassword(body: any): Promise<any> {
    const res = http.post('api/students/change-password', body)
    return res
  },

}

export default authorizationServices