
import * as Yup from 'yup';

export const basicInformationRules = Yup.object({
      first_name: Yup.string().max(50, 'Maximum 50 character.').required('Required'),
      gender_identity_description: Yup.string().max(100, 'Maximum 100 character.'),
      last_name: Yup.string().max(50, 'Maximum 50 character.').required('Required'),
      middle_name: Yup.string().max(50, 'Maximum 50 character.'),
      nick_name: Yup.string().max(20, 'Maximum 20 character.'),
})