import Header from 'components/layout/header';
import MobileHeader from 'components/layout/MobileHeader';
import SideBar from 'components/layout/sideBar';
import React from 'react';
import { Container } from 'react-bootstrap';
import { Switch } from 'react-router';
import ProtectedRoute from './ProtectedRoute';
import { appRoutes } from './router';

interface Props {}

const DashBoardLayout = (props: Props) => {
  return (
    <Container fluid className="vh-100 p-1 overflow-hidden">
      <div className="d-none d-sm-block w-100">
        <Header title="Dashboard" />
      </div>
      <div className="d-block d-sm-none w-100">
        <MobileHeader />
      </div>
      <div className="w-100  d-flex" style={{ height: '100%' }}>
        <div className="d-none d-sm-block border-end" style={{ width: '48px', height: '100%' }}>
          <SideBar hideList />
        </div>
        <div className="w-100 h-100 overflow-auto pb-5">
          <Switch>
            {Object.keys(appRoutes).map((route: any, index: any) => {
              return (
                <ProtectedRoute
                  key={index}
                  path={appRoutes[route].path}
                  component={appRoutes[route].component}
                  exact
                />
              );
            })}
          </Switch>
        </div>
      </div>
    </Container>
  );
};

export default DashBoardLayout;
