import React from 'react';
import { Col, Form } from 'react-bootstrap';

interface Props {
  tooltip?: string;
  mdCol: string;
  label?: string;
  name: string;
  values: any;
  onChange: (e: any) => void;
  isValid?: boolean;
  feedback?: any;
  placeholder?: string;
}

const TextAreaInput = (props: Props) => {
  return (
    <div>
      <Form.Group className="mb-3" as={Col} md={props.mdCol || '6'} controlId="validationFormik01">
        <Form.Label>{props.label}</Form.Label>
        <div className="text-muted" style={{ fontSize: '0.8rem' }}>
          {props.tooltip}
        </div>
        <Form.Control
          as="textarea"
          rows={3}
          placeholder={props.placeholder}
          name={props.name}
          value={props.values}
          onChange={props.onChange}
          isValid={props.isValid}
        />
        {props.feedback}
      </Form.Group>
    </div>
  );
};

export default TextAreaInput;
