import React from 'react';
import { Col, Form } from 'react-bootstrap';

interface Props {
  label?: string;
  mdCol?: string;
  name?: string;
  values?: any;
  onChange?: (e: any) => void;
  isValid?: boolean;
  feedback?: any;
  placeholder?: string;
}

const SelectPhoneCodeInput = (props: Props) => {
  return (
    <Form.Group name={props.name} as={Col} md={props.mdCol || '6'} controlId="formGridState">
      <Form.Label>{props.label}</Form.Label>
      <Form.Select onChange={props.onChange} name={props.name} value={props.values}>
        {[{ name: 'United States', dial_code: '+1', code: 'US' }].map((opt, index) => (
          <option key={index} value={opt.dial_code}>
            {opt.name} {opt.dial_code}
          </option>
        ))}
      </Form.Select>
    </Form.Group>
  );
};

export default SelectPhoneCodeInput;
