import React from 'react';
import { Col, Form } from 'react-bootstrap';

interface Props {
  borderless?: boolean;
  label?: string;
  option: any[];
  mdCol?: string;
  name?: string;
  values?: any;
  onChange?: (e: any) => void;
  isValid?: boolean;
  feedback?: any;
  placeholder?: string;
}

const SelectInput = (props: Props) => {
  return (
    <Form.Group
      className="mb-3"
      name={props.name}
      as={Col}
      md={props.mdCol || '6'}
      controlId="formGridState"
    >
      <Form.Label>{props.label}</Form.Label>
      <Form.Select onChange={props.onChange} name={props.name} value={props.values}>
        {props.option.map((opt, index) => (
          <option key={index} value={opt.id || opt.value}>
            {opt.name || opt.value}
          </option>
        ))}
      </Form.Select>
    </Form.Group>
  );
};

export default SelectInput;
